import { NavLink } from 'react-router-dom';
import bot from './imgs/bot.png';

const header = () => {
    return (
        <>
            <div className="container-fluid navbar-style py-3">
                <div className="row">
                    <div className="col-md-10 col-12 mx-auto">

                        <nav className="navbar navbar-expand-lg navbar-light bg-light">
                            <NavLink className="navbar-brand" id="logo" to="/">Cyan</NavLink>
                            <img src={bot} alt="Bot" width="100vw" height="50vh" className="d-inline-block align-text-top" />
                            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon"></span>
                            </button>

                            <div className="collapse navbar-collapse" id="navbarNavDropdown">
                                <ul className="navbar-nav ml-auto">
                                    <li className="nav-item active">
                                        <a className="nav-link" href="#serviceid">Services</a>
                                    </li>
                                    <li className="nav-item active">
                                        <a className="nav-link" href="#featuresid">Features</a>
                                    </li>
                                    <li className="nav-item active">
                                        <NavLink className="nav-link" to="/Gallery">Gallery</NavLink>
                                    </li>
                                    <li className="nav-item active">
                                    <NavLink className="nav-link" to="/Contact">Contact</NavLink>
                                    </li>
                                </ul>
                            </div>
                        </nav>

                    </div>
                </div>
            </div>
        </>
    );
};

export default header;